div.dt-button:hover:not(.disabled),
a.dt-button:hover:not(.disabled),
input.dt-button:hover:not(.disabled) {
    border: 1px solid #666;
    background-color: $black-1;
    background: -webkit-linear-gradient(top, rgba(153, 153, 153, 0.1) 0%, $black-1 100%);
    background: -moz-linear-gradient(top, rgba(153, 153, 153, 0.1) 0%, $black-1 100%);
