.dt-button embed {
    outline: none;
}

div {
    &.dt-buttons {
        position: relative;
        float: left;

        &.buttons-right {
            float: right;
        }
    }

    &.dataTables_layout_cell div.dt-buttons {
        float: none;

        &.buttons-right {
            float: none;
        }
    }

    &.dt-button-collection {
        position: absolute;
        top: 0;
        left: 0;
        width: 150px;
        margin-top: 3px;
        padding: 8px 8px 4px 8px;
        border: 1px solid #ccc;
        border: 1px solid $black-4;
        background-color: white;
        overflow: hidden;
        z-index: 2002;
        border-radius: 5px;
        box-shadow: 3px 3px 5px $black-3;
        box-sizing: border-box;

        button.dt-button,
        div.dt-button,
        a.dt-button {
            position: relative;
            left: 0;
            right: 0;
            width: 100%;
            display: block;
            float: none;
            margin-bottom: 4px;
            margin-right: 0;
        }

        button.dt-button {
            &:active:not(.disabled),
            &.active:not(.disabled) {
                background-color: #dadada;
                background: -webkit-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: -moz-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: -ms-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: -o-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: linear-gradient(to bottom, #f0f0f0 0%, #dadada 100%);
                filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0,StartColorStr="#f0f0f0", EndColorStr="#dadada");
                box-shadow: inset 1px 1px 3px #666;
            }
        }

        div.dt-button {
            &:active:not(.disabled),
            &.active:not(.disabled) {
                background-color: #dadada;
                background: -webkit-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: -moz-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: -ms-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: -o-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: linear-gradient(to bottom, #f0f0f0 0%, #dadada 100%);
                filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0,StartColorStr="#f0f0f0", EndColorStr="#dadada");
                box-shadow: inset 1px 1px 3px #666;
            }
        }

        a.dt-button {
            &:active:not(.disabled),
            &.active:not(.disabled) {
                background-color: #dadada;
                background: -webkit-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: -moz-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: -ms-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: -o-linear-gradient(top, #f0f0f0 0%, #dadada 100%);
                background: linear-gradient(to bottom, #f0f0f0 0%, #dadada 100%);
                filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0,StartColorStr="#f0f0f0", EndColorStr="#dadada");
                box-shadow: inset 1px 1px 3px #666;
            }
        }

        &.fixed {
            position: fixed;
            top: 50%;
            left: 50%;
            margin-left: -75px;
            border-radius: 0;

            &.two-column {
                margin-left: -200px;
            }

            &.three-column {
                margin-left: -225px;
            }

            &.four-column {
                margin-left: -300px;
            }
        }

        > :last-child {
            display: block !important;
            -webkit-column-gap: 8px;
            -moz-column-gap: 8px;
            -ms-column-gap: 8px;
            -o-column-gap: 8px;
            column-gap: 8px;

            > * {
                -webkit-column-break-inside: avoid;
                break-inside: avoid;
            }
        }

        &.two-column {
            width: 400px;

            > :last-child {
                padding-bottom: 1px;
                -webkit-column-count: 2;
                -moz-column-count: 2;
                -ms-column-count: 2;
                -o-column-count: 2;
                column-count: 2;
            }
        }

        &.three-column {
            width: 450px;

            > :last-child {
                padding-bottom: 1px;
                -webkit-column-count: 3;
                -moz-column-count: 3;
                -ms-column-count: 3;
                -o-column-count: 3;
                column-count: 3;
            }
        }

        &.four-column {
            width: 600px;

            > :last-child {
                padding-bottom: 1px;
                -webkit-column-count: 4;
                -moz-column-count: 4;
                -ms-column-count: 4;
                -o-column-count: 4;
                column-count: 4;
            }
        }

        .dt-button {
            border-radius: 0;
        }
    }

    &.dt-button-background {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $black-7;
        background: -ms-radial-gradient(
            center,
            ellipse farthest-corner,
            $black-3 0%,
            $black-7 100%
        );
        background: -moz-radial-gradient(
            center,
            ellipse farthest-corner,
            $black-3 0%,
            $black-7 100%
        );
        background: -o-radial-gradient(center, ellipse farthest-corner, $black-3 0%, $black-7 100%);
        background: -webkit-gradient(
            radial,
            center center,
            0,
            center center,
            497,
            color-stop(0, $black-3),
            color-stop(1, $black-7)
        );
        background: -webkit-radial-gradient(
            center,
            ellipse farthest-corner,
            $black-3 0%,
            $black-7 100%
        );
        background: radial-gradient(ellipse farthest-corner at center, $black-3 0%, $black-7 100%);
        z-index: 2001;
    }
}

@media screen and (max-width: 640px) {
    div.dt-buttons {
        float: none !important;
        text-align: center;
    }
}

button.dt-button.processing,
div.dt-button.processing,
a.dt-button.processing {
    color: $black-2;
}

button.dt-button.processing:after,
div.dt-button.processing:after,
a.dt-button.processing:after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 16px;
    height: 16px;
    margin: -8px 0 0 -8px;
    box-sizing: border-box;
    display: block;
    content: " ";
    border: 2px solid #282828;
    border-radius: 50%;
    border-left-color: transparent;
    border-right-color: transparent;
    animation: dtb-spinner 1500ms infinite linear;
    -o-animation: dtb-spinner 1500ms infinite linear;
    -ms-animation: dtb-spinner 1500ms infinite linear;
    -webkit-animation: dtb-spinner 1500ms infinite linear;
    -moz-animation: dtb-spinner 1500ms infinite linear;
}
